
@font-face {
  font-family: Roboto;
  src: url('./assets/Roboto-Regular.ttf')
}
body, button, input, textarea {
  font-family: Roboto;
  border: none;
}
button:focus, input:focus, textarea:focus {
  outline: none;
}
body {
  margin: 0;
  background-color: #222;
}
a {
  color: inherit;
  text-decoration: none;
}
.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}
.container {
  background-color: #111;
  border-left-width: 4px;
  border-left-style: solid;
}
.container-auth {
  border-color: #2CA6AB;
}
.container-post {
  border-color: #2E9D51;
}
.container-agnostic {
  border-color: #888;
}
.form-label {
  color: #eee;
  font-size: 18px;
  margin-bottom: 13px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.form-input {
  background-color: transparent;
  font-size: 15px;
  color: #ccc;
  border-bottom: 1px solid;
  width: 200px;
}
.form-input-auth {
  border-bottom-color: #2CA6AB;
}
.form-input-post {
  border-bottom-color: #2E9D51;
}
.form-input-agnostic {
  border-bottom-color: #888;
}
.form-button-layer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.form-button {
  width: 200px;
  height: 33px;
  color: #eee;
  font-size: 18px;
  cursor: pointer;
  margin: auto;

  transition: background-color .5s, border-color .5s;
}
a.form-button {
  display: inline-block;
  text-align: center;
  line-height: 33px;
}
.form-button-inverted.form-button-inverted {
  background-color: transparent;
  border-width: 2px;
  border-style: solid;
}
.form-button-auth {
  background-color: #195D60;
  border-color: #195D60;
}
.form-button-post {
  background-color: #2E9D51;
  border-color: #2E9D51;
}
.form-button-asset {
  background-color: #5B34BE;
  border-color: #5B34BE;
}
.form-button-user {
  background-color: #8D7E27;
  border-color: #8D7E27;
}
.form-button-danger {
  background-color: #9E2E2E;
  border-color: #9E2E2E;
}
.form-info {
  padding-top: 9px;
  height: 20px;
  color: #ddd;
  margin: auto;
  font-size: 13px;
}
.avatar {
  border-radius: 100%;
  width: 42px;
  height: 42px;
}



